import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const Demo = function () {
  return (
    <div>
      <h2>Addition ClassName</h2>
      <Datepicker
        placeholder={'点击选择日期'}
        className={'input-border-red'}
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo7')
);
